import { requireUser } from "@/lib/auth-guard";
import { ok, bad } from "@/lib/api-respond";

export async function GET(req: Request) {
  try {
    const { supabase, user } = await requireUser(req);

    const { data, error } = await supabase
      .from("driver_state")
      .select(
        "driver_id,last_ping_at,lat,lon,snapped_zone_id,speed_kmh,battery_pct,recommendation,updated_at"
      )
      .eq("driver_id", user.id)
      .maybeSingle();

    if (error && error.code !== "PGRST116") {
      console.error("[driver-state] fetch failed", {
        driverId: user.id,
        error,
      });
      return bad("Failed to fetch driver state", null, 500, "DB_ERROR");
    }

    if (!data) {
      return new Response(null, { status: 204 });
    }

    return ok({
      driver_id: data.driver_id,
      last_ping_at: data.last_ping_at,
      snapped_zone: data.snapped_zone_id,
      snapped_zone_id: data.snapped_zone_id,
      lat: data.lat,
      lon: data.lon,
      speed_kmh: data.speed_kmh,
      battery_pct: data.battery_pct,
      recommendation: data.recommendation,
      updated_at: data.updated_at,
    });
  } catch (error: any) {
    if (error.message === "Unauthorized") {
      return bad("Unauthorized", null, 401, "UNAUTHORIZED");
    }
    console.error("[driver-state] unexpected error", error);
    return bad("Unexpected error", null, 500, "INTERNAL_ERROR");
  }
}
